<template>
  <div id="home-icon" v-if="state != 0" @click="reloadPage">
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width="16"
      height="16"
      fill="currentColor"
      class="bi bi-house-fill"
      viewBox="0 0 16 16"
    >
      <path
        d="M8.707 1.5a1 1 0 0 0-1.414 0L.646 8.146a.5.5 0 0 0 .708.708L8 2.207l6.646 6.647a.5.5 0 0 0 .708-.708L13 5.793V2.5a.5.5 0 0 0-.5-.5h-1a.5.5 0 0 0-.5.5v1.293L8.707 1.5Z"
      />
      <path d="m8 3.293 6 6V13.5a1.5 1.5 0 0 1-1.5 1.5h-9A1.5 1.5 0 0 1 2 13.5V9.293l6-6Z" />
    </svg>
    <p>Home</p>
  </div>

  <h1>
    üç∏ Cocktails <span>{{ selected }}</span>
  </h1>
  <div id="ingredients" v-if="state === 0">
    <input type="text" v-model="searchQuery" placeholder="Buscar ingrediente" style="width: 100%" />
    <p
      v-for="ingredient in filterIngredients()"
      :key="ingredient.strIngredient1"
      :style="`background-color: ${ingredient.color}`"
      @click="searchIngredient(ingredient.strIngredient1)"
    >
      {{ ingredient.strIngredient1 }}
    </p>
  </div>

  <div id="drinks" v-if="state == 1">
    <div
      v-for="drink in this.drinks"
      :key="drink.idDrink"
      class="drink"
      :style="`background-image: url(${drink.strDrinkThumb});`"
      @click="getDrink(drink.idDrink)"
    >
      <p>
        {{ drink.strDrink }}
      </p>
    </div>
  </div>

  <div id="drink" v-if="state == 2">
    <h1>
      {{ drink.strDrink }} /
      <span>{{ drink.strAlcoholic }}</span>
    </h1>
    <img :src="drink.strDrinkThumb" alt="Drink picture" />
    <div class="info">
      <p>Tipo de vaso: {{ drink.strGlass }}</p>
      <br />
      <p>Ingredients: ‚¨áÔ∏è</p>
      <div v-for="(ingredient, index) in drink.ingredients" :key="index" style="display: flex">
        <p>&nbsp;&nbsp;&nbsp;{{ ingredient }}</p>
        <p v-if="drink.measures[index]">- {{ drink.measures[index] }}</p>
      </div>
      <br />
      <p>Instruciones:<br />{{ drink.strInstructions }}</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      state: 0,
      selected: "",
      allIngredients: [],
      filteredIngredients: [],
      drinks: [],
      drink: {},
      searchQuery: "",
    };
  },
  name: "App",
  components: {
    // StarshipsComponent,
  },
  methods: {
    getIngredients() {
      if (this.allIngredients.length === 0) {
        fetch("https://www.thecocktaildb.com/api/json/v1/1/list.php?i=list")
          .then((response) => response.json())
          .then((data) => {
            this.allIngredients = data.drinks;

            // Asignar colores a los ingredientes
            this.allIngredients.forEach((ingredient) => {
              ingredient.color = this.getRandomColor();
            });

            this.filteredIngredients = this.allIngredients;
          })
          .catch((error) => console.error(error));
      }
    },
    getRandomColor() {
      const color = Math.floor(Math.random() * 16777215).toString(16);
      return `#${color}80`;
    },
    filterIngredients() {
      if (this.searchQuery === "") {
        return this.allIngredients;
      } else {
        return this.filteredIngredients.filter((ingredient) =>
          ingredient.strIngredient1.toLowerCase().startsWith(this.searchQuery.toLowerCase())
        );
      }
    },

    randomColor() {
      const color = Math.floor(Math.random() * 16777215).toString(16);
      return `#${color}80`;
    },
    searchIngredient(ingredient) {
      this.selected = ingredient;
      this.state = 1;

      fetch("https://www.thecocktaildb.com/api/json/v1/1/filter.php?i=" + ingredient)
        .then((response) => response.json())
        .then((data) => {
          this.drinks = data.drinks;
        })
        .catch((error) => console.error(error));
    },
    getDrink(id) {
      this.state = 2;

      fetch("https://www.thecocktaildb.com/api/json/v1/1/lookup.php?i=" + id)
        .then((response) => response.json())
        .then((data) => {
          // Save drink
          this.drink = data.drinks[0];

          this.selected = this.drink.strCategory;

          // Afegim claus al objecte drink
          this.drink.ingredients = [];
          this.drink.measures = [];

          // Group ingredients and measures in array
          for (const key in this.drink) {
            const value = this.drink[key];

            if (value != null) {
              if (key.includes("strIngredient")) {
                this.drink.ingredients.push(value);
              }
              if (key.includes("strMeasure")) {
                this.drink.measures.push(value);
              }
            }
          }
        })
        .catch((error) => console.error(error));
    },
    reloadPage() {
      this.state = 0;
      this.selected = "";
      this.drinks = [];
      this.drink = {};
    },
  },
  mounted() {
    this.getIngredients();
  },
};
</script>

